import React, { Component } from 'react';

import './one_circle.css';

class OneCircle extends Component {
	constructor(props) {
		super(props);
		this.state = {x: 0, y: 0};
	}

	componentDidMount() {
		window.addEventListener("mousemove", this.handleMouseClick);
	}

	componentWillUnmount() {
		window.removeEventListener("mousemove", this.handleMouseClick);
	}

	_onMouseMove(e) {
    	this.setState({ x: e.nativeEvent.offsetX, y: e.nativeEvent.offsetY });
	}

  	render() {
  		const { x, y } = this.state;

  		console.log(x);
    	return (
   			<div onMouseMove={this._onMouseMove.bind(this)} class="one_circle">
   				{x}
   			</div>
		);
  	}
}

export default OneCircle;



